.abt{
     background-color: #016e4215;
     margin: 0;
     border-radius: 20px;
     padding: 1em 0;
}
.me-img{
     width: 15em;
     height: 15em;
     border-radius: 50%;
     margin: 2em auto;
}
.c-me{
     color: #00B169;
     font-size: 1.5rem;;
}
.c-links{
     text-decoration: none;
     list-style-type: none;
     text-align: center;
     margin: 2em auto;
}
.c-links li{
     margin: .5em auto;
     color: #00B169;
     font-weight: bold;
}
.c-links a{
     text-decoration: none;
     color: #05c578a4;
}
.c-links a:hover{
     text-decoration: underline;
}
.abt-link{
     color: white;
     text-decoration: none;
}

